.wether-container{
    background-repeat:no-repeat;
    background-size: cover;
    height: 100vh;
}
.day{
    background-image: url('day-large.png');
}
.night{
    background-image: url('night-large.svg');
}

@media screen and (max-width:600px) {
    .day{
        background-image: url('day.png');
    }
    .night{
        background-image: url('night.svg');
    }
}